import React from 'react';
import { useSelector } from 'react-redux';
import { isArray } from '../../../utils/validations/helperFunc';
import { renderErrorTxt } from '../../../utils/validations/Investor-Application-Form/formErrorUtils';

const CustomTextField = ({ placeholder, id, label, type, asterisk, belowText, inputType, name, value, onChangeFn, formErrors={}, maxDate }) => {
  const invFormStatus = useSelector((store) => store.invFormStatus);

  const onChangeHandle = (e) =>{
    const {name, value} = e.target;
    onChangeFn({name, value}) 
  }

  const renderErrors = () => {

    

    if(formErrors[name]){
      if(isArray(formErrors[name])){
        return renderErrorTxt(formErrors[name])
      } else{
        return renderErrorTxt([formErrors[name]])
      }
    }
    return null

    // return formErrors[name]? renderErrorTxt(formErrors[name]) : null
  }

  const isIncorrect = () => {
    if(formErrors[name]){
      if(isArray(formErrors[name])){
        // return renderErrorTxt(formErrors[name])
        return !!formErrors[name][0]?.length
      } else{
        return !!formErrors[name]?.length
      }
    }
    return false
  }
  
  return (
    <div className={`form-input ${type? type : ""} ${isIncorrect()? 'incorrect':''}`}>
        <label htmlFor={id}>{label} {asterisk !==""? <span>*</span>: null}</label>
        <input className="form-input-common" type={inputType? inputType : "text"} placeholder={placeholder} id={id} required={asterisk !==""? true: false} name={name} value={value || ""} onChange={onChangeHandle} disabled={!!invFormStatus.loader} max={maxDate || null}/>
        
        {belowText? <div className="input__desc">
          <p className="input__desc--p">{belowText}</p>
        </div>: null}

        {renderErrors()}
    </div>
  )
}

export default CustomTextField;