import React from 'react';
import { useSelector } from 'react-redux';
import CustomRadioInput from '../../Layout/CustomRadioInput';
import { ONE_SIGNATORY, TWO_SIGNATORY, ALL_SIGNATORY } from '../../../../constants';
import { renderErrorTxt } from '../../../../utils/validations/Investor-Application-Form/formErrorUtils';

function SigningAuthority({onFormChange}) {
    const secI = useSelector((store)=>store.secI);
    const invFormStatus = useSelector((store) => store.invFormStatus);
    
    const onChangeFn = ({name, value}) =>{ 
        const convrtToNum = parseInt(value);
        onFormChange({name, value:convrtToNum, level:''});
    }
    
  return (
    <>
        <h4 className="section__container--subtitle">Ongoing Signatory Authority</h4>
        <div>
            <h5>Signing Authority <span>*</span></h5>
            <div>
                <CustomRadioInput 
                    name="signing_authority" 
                    id="only-one-required-to-sign"
                    val={ONE_SIGNATORY}
                    content="Only one required to sign"
                    checked={secI.signing_authority === ONE_SIGNATORY}
                    onChangeFn={onChangeFn}
                />
                <CustomRadioInput
                    name="signing_authority"
                    id="two-signatories-must-sign"
                    val={TWO_SIGNATORY}
                    content="Two signatories must sign"
                    checked={secI.signing_authority === TWO_SIGNATORY}
                    onChangeFn={onChangeFn}
                />
                <CustomRadioInput
                    name="signing_authority"
                    id="all-signatories-must-sign"
                    val={ALL_SIGNATORY}
                    content="All signatories must sign (joint accounts only)"
                    checked={secI.signing_authority === ALL_SIGNATORY}
                    onChangeFn={onChangeFn}
                />
            </div>
            {invFormStatus.errors?.signing_authority? <div className="input__desc">
                {renderErrorTxt(invFormStatus.errors?.signing_authority)}
            </div>: null}
        </div>

        <div className="note__box u-padding-10y">
            <p className="note__box--p">Please indicate signing requirements for future instructions regarding your investment</p>
        </div>
    </>
  )
}

export default SigningAuthority