import React, { useState } from 'react';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faCaretDown, faCaretUp } from '@fortawesome/free-solid-svg-icons';
import DropdownList from './DropdownList';

const CustomDropdown = ({ dropdownOptions, selectedOption, updateSelectedOption, addClass="", extraLabel="", doNotShowExtraLabelSpecial=[] }) => {

  const [ toggle, setToggle ] = useState(false);

  const onClickVisible = () => setToggle( prevState => !prevState )

  const onSelectOption = ({label, value}) => {
    updateSelectedOption({label, value})
    onClickVisible();
  }

  const renderLabel = (option) =>{
    const mainLabel = option?.label || ""

    if(doNotShowExtraLabelSpecial.includes(option?.value)){
      return mainLabel
    } else if(option?.label && extraLabel){
      return `${option.label} ${extraLabel}`
    }
    return mainLabel

  }

  return (
        <div className="dropdown__main">
            <div className="dropdown__main--selected" onClick={onClickVisible}>
                <label>{renderLabel(selectedOption)}</label>
                <FontAwesomeIcon icon={toggle? faCaretUp : faCaretDown} />
            </div>
            {toggle && <DropdownList
            onSelectOption={onSelectOption}
            dropdownOptions={dropdownOptions}
            addClass={addClass}
            renderLabel={renderLabel}
            onClickVisible={onClickVisible}
            />}  
        </div>
  )
}

export default CustomDropdown;