import { LOADING } from "../../constants";

export const addNewDocToArray = ({files, uploadedDocs, setUploadedDocs, onUploadFile, selectedDocType, selectedEntityNum={}, doNotShowExtraLabel=[]}) => {
    const uploaded = [...uploadedDocs];
        
        files.some((file) => { 
            if(uploaded.findIndex((f) => f.file.name === file.name) === -1){
                //call upload file --
                onUploadFile(file, {...selectedDocType}, {...selectedEntityNum});
                uploaded.push({ 
                    file,
                    loader: LOADING,
                    docUrl: "",
                    type: {...selectedDocType},
                    ...(doNotShowExtraLabel?.length ? !doNotShowExtraLabel.includes(selectedDocType.value) && {extraDetails: {...selectedEntityNum}} : false), //add extra details if array exists and value is not from array
                });
            }
            //return a true if you want to cancel file adding and call api process -- (for example to put a limit on no.of files)
        });
        
        setUploadedDocs(uploaded);
}

export const updateDocArray = ({setUploadedDocs, fileName, extradata={}}) => {
    setUploadedDocs((prevDocs) => { //set the state of loader w.r.t file name
        return prevDocs.map((each)=>{
            if(each.file.name === fileName){ 
                return {
                    ...each,
                    ...extradata
                }
            }
            return each
        })
    })
}

export const removeDocFromArray = ({fileName, uploadedDocs, setUploadedDocs}) => { 
    const allFiles = [...uploadedDocs];
    const newSetFiles = allFiles.filter((each)=>{
        return each.file.name !== fileName;
    });
    setUploadedDocs([...newSetFiles]);
}