import React from 'react';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faCheck } from '@fortawesome/free-solid-svg-icons';
import { faTimes } from '@fortawesome/fontawesome-free-solid';
import { faTrashAlt } from '@fortawesome/free-solid-svg-icons';
import { LoaderOnConfirm } from '../../stylesheet/Loader';
import { LOADING , SUCCESS, FAILED } from '../../../constants';

const DocPlaceholder = ({uploadedDocs=[], onAddFiles, onClickRemove, onClickDel, multiple=true, accept="application/pdf, image/*", uniqId}) => {

  const allowedTypes = ["image/jpg", "image/jpeg", "image/png", "application/pdf"];

  const manipulateUI = (e) => {
    const chosenFiles = Array.prototype.slice.call(e.target.files) || []
    const validFiles = chosenFiles.filter((file)=> allowedTypes.includes(file.type))

    onAddFiles(validFiles);
  }

  const removeFileFromFileList = (fileName) => {
    const dt = new DataTransfer();
    const input = document.getElementById(`file-doc-in-${uniqId}`);
    const { files } = input;
    
    for (let i = 0; i < files.length; i++) {
      const file = files[i];

      if (fileName !== file.name){
        dt.items.add(file); // add the file back to input.
      }
    }
    
    input.files = dt.files // Assign the updates list
  }

  const onHandleClear = (fileName)=>{
    onClickRemove(fileName);
    removeFileFromFileList(fileName);
  }

  const onHandleDelete = ({fileName, fileData}) => {
    onClickDel({fileName, docData: fileData})
    removeFileFromFileList(fileName);
  }

  const renderDel = ({fileName, fileData}) => {
    if(fileData?.delLoader) {
      return <span style={{marginLeft:10}}> <LoaderOnConfirm /> </span>
    }
    return (<span
              style={{marginLeft:10}}
              onClick={()=>onHandleDelete({fileName, fileData})}
            >
              <FontAwesomeIcon className='docPreviewDelIcon' icon={faTrashAlt}/>
            </span>)
  }

  const renderStatus = (status, fileName, fileData) => {
    if(status === SUCCESS) {  //return success icon
      return(
        <>
          <FontAwesomeIcon icon={faCheck}/>
          {renderDel({fileName, fileData})}
        </>
      ) 
    }else if(status === FAILED) {
      return <button className="link-button" onClick={()=> onHandleClear(fileName)}>upload failed <FontAwesomeIcon icon={faTimes}/></button> //status is failed - user shall clear file from list & retry
    }else if(status === LOADING){
      return <LoaderOnConfirm /> //return loader icon
    }
  }

  const renderFileLabel = (fileData) => {
    
    if(fileData?.type?.label && fileData?.extraDetails?.label){
      return `(${fileData.type.label} - ${fileData?.extraDetails?.label})`
    } else if(fileData?.type?.label){
      return `(${fileData.type.label})`
    }
    return ""
  }

  const mapUploadedDocs = () => {
    return (
      <div className="docPlaceholder__uploaded">
        {uploadedDocs.map((each)=> {
          return (
            <div className="docPlaceholder__uploaded--responses" key={each.file.name}>
              <a className="link-button" target="_blank" href={each?.docUrl? each?.docUrl: null} rel="noreferrer">{each.file.name}</a><span className='uploaded-file-label'>{renderFileLabel(each)}</span>
              <span style={{marginLeft:5}}>{renderStatus(each.loader, each.file.name, each)}</span>
            </div>
          )
        })} 
      </div>
    )
  }
    
  return (
    <React.Fragment>
      <div className="docPlaceholder">
        <div className="docPlaceholder__container">
          <input type="file" id={`file-doc-in-${uniqId}`} className={`docPlaceholder__container--input ${false? 'doc-opacity': ''}`} onChange={manipulateUI} multiple={multiple} accept={accept}/>
          <label htmlFor={`file-doc-in-${uniqId}`} id="uploadDocLabel" className="docPlaceholder__container--p">Drag your documents here to start uploading</label>
        </div>
      </div>
      {mapUploadedDocs()}
    </React.Fragment>
  )
}

export default DocPlaceholder;