import React, {useState} from 'react';
import { useSelector } from 'react-redux';
import DocPlaceholder from '../../Layout/DocPlaceholder';
import InvestorSignatory from './InvestorSignatory';
import { SIGN_MANDATORY_I, LOADING, SUCCESS, FAILED, SUPPORTING_DOCUMENTS } from '../../../../constants';
import { uploadFiles, deleteFile } from '../../../../actions/Investor-Application-Form/uploadDocActions';
import { formatAndPrintDocs } from '../../../../utils/validations/helperFunc';
import CustomDropdown from '../../Layout/CustomDropdown';
import { addNewDocToArray, removeDocFromArray, updateDocArray } from '../../../../utils/documentUploads';
import { useToast } from '../../../CustomToast';
import SigningAuthority from './SigningAuthority';

const SectionIContent = ({ onFormChange, investorFormId, removeDocFromArr }) => {
    const toast = useToast();
    const secI = useSelector((store) => store.secI);

    const [selectedDocType, setSelectedDocType] = useState({label:SUPPORTING_DOCUMENTS[0][1], value:SUPPORTING_DOCUMENTS[0][0]})
    const [uploadedDocs, setUploadedDocs] = useState([])

    const onChangeSignatory = ({name, value, level, investorId}) => {
        onFormChange({name, value:value, level, investor_id:investorId});
    }

    const onUploadFile = async(file, type={label:'', value:''}) =>{ //call api to upload files
        if(file){                                 
            const fileName = file.name                   
            const formData = new FormData();
            formData.append('file', file);
            formData.append('type', type.value);
            formData.append('section', 'i');
            formData.append('investor_form_id', investorFormId);
            const {success, url, data} = await uploadFiles(formData)
            const extradata = {
                loader: success? SUCCESS : FAILED,
                docUrl: url,
                resData: data
            }
            updateDocArray({setUploadedDocs, fileName, extradata})
        }
    }

    const onAddFiles = (files) => {
        //add selected files(by user) to local state - and call api
        addNewDocToArray({
            files,
            uploadedDocs,
            setUploadedDocs,
            onUploadFile,
            selectedDocType:{...selectedDocType},
        })
    }

    // const onClickRetry = (fileName) => {
    //     const reqFile = uploadedDocs.find((each)=> {
    //         const x = each?.file?.name === fileName
    //         console.log(x, each?.file?.name, fileName );
    //         return x
    //     });
    //     if(reqFile) {
    //         //call upload file --
    //         onUploadFile(reqFile.file);
    //     }
    // }

    const onClickRemove = (fileName) => {
        //just remove the file from local state
        removeDocFromArray({fileName, uploadedDocs, setUploadedDocs})
    }

    const onClickDel = async({fileName, docData}) => {
        //delete the file from aws & remove the file from local state
        updateDocArray({setUploadedDocs, fileName, extradata: {delLoader: true}})
        const { success } = await deleteFile(docData?.resData);
        success && onClickRemove(fileName);
        success && toast.success("Deleted successfully!");
        success || updateDocArray({setUploadedDocs, fileName, extradata: {delLoader: false}})
        success || toast.error("Please try again!")
    }

    const updateSelectedOption = (option) => {
        //update selected document type to the state
        setSelectedDocType({...option})
    }

    const renderInvestorSignatory = () => {
        return secI?.individual_details?.map((eachItem, arrIndex)=>{
            const tempId = arrIndex+1
            // const investorId = eachItem?.signatory_info_id || tempId
            const investorId = eachItem?.index || tempId
            const mandatory = arrIndex < SIGN_MANDATORY_I // //mandatory only till 2 directors/investors

            return <InvestorSignatory key={investorId} investorId={investorId} investorFormId={investorFormId} investorDetails={eachItem} onChangeSignatory={onChangeSignatory} mandatory={mandatory} itemIndex={arrIndex}/> 
        })
    }

  return (
    <div className="section">
        <h2 className="section__heading" title="Section I: Declarations & Signatures">Section I: Declarations &amp; Signatures</h2>
        <div className="section__container">
            <div>
                <h4 className="section__container--subtitle u-padding-10y">Please read the declarations below before signing this Application Form.</h4>

                <div className="section__container--box">
                    <h5>Use of email</h5>
                    <div>
                        <div className="terms">
                        <p>If I/we submit my/our Application Form by email, the Trustee may rely on the emailed Application Form to process and accept my/our application. For each investment in the Fund, if the Trustee receives instructions by email in relation to my/our investment in the Fund, I/we:</p>
                        </div>
                        <ol className="terms">
                            <li>Will send the originals in the mail for the Trustee's records,</li>
                            <li>Acknowledge that there is potentially a greater risk that fraudulent instructions can be given by someone who has access to my/our account number and a copy of my/our signature(s) and that I/we accept such risks,</li>
                            <li>Acknowledge that the Trustee may assume that the instruction has been sent, and is authorised, by or on behalf of me/us, and</li>
                            <li>Release the Trustee from, and indemnify the Trustee against, all losses and liabilities arising from any payment or action taken by the Trustee based on any instruction bearing my/our account number and a signature that purports to be mine/ours or that of an authorised signatory on the account, even if such instructions are not authorised (except to the extent that such losses and liabilities directly arise from the negligence or wilful default of the Trustee).</li>
                        </ol>
                    </div>
                </div>
                
                <div className="section__container--box">
                    <h5>Consent for Electronic ID Verification</h5>
                    <div className="terms">
                        <p>I/We hereby consent and authority for Specialised Investment and Lending Corporation Pty Ltd and its related entity (SILC) to provide and use my personal information (including my passport details) for the purpose of meeting SILC’s obligations under Australian law in relation to identity verification (such as AML/CTF Laws) including checking my information with the document issuer or official record holder via third party systems.</p>
                        <p>In the event that Electronic ID Verification is unable to be completed, we may be required to conduct the ID verification via video call.</p>
                    </div>
                </div>

                {/* {Array.from({ length: numOfInvestors }, (_, index) => <InvestorSignatory investorId={index} key={index}/>)} */}
                {renderInvestorSignatory()}
                <div className="section__container--box">
                    
                    {
                        secI?.no_of_investors !== 1? <SigningAuthority onFormChange={onFormChange}/> : null
                    }                       

                    <div>
                        <h6 className="u-padding-y u-margin-bottom-lg">Upload other supporting documents (if any) before submitting the application</h6>
                        <CustomDropdown
                            dropdownOptions={SUPPORTING_DOCUMENTS}
                            selectedOption={selectedDocType}
                            updateSelectedOption={updateSelectedOption}
                        />
                        {selectedDocType?.value ?
                            <div className='u-padding-y'>
                                <DocPlaceholder onAddFiles={onAddFiles} uploadedDocs={uploadedDocs} onClickRemove={onClickRemove} onClickDel={onClickDel} uniqId="sectionicontentdocs"/>
                            </div>: null
                        }
                    </div>
                </div>
            </div>

            {secI?.documents?.length > 0? formatAndPrintDocs(secI?.documents, removeDocFromArr) : null} 
        </div>
    </div>
  )
}

export default SectionIContent;