import React, { useRef } from 'react';
import SignaturePad from 'react-signature-canvas';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faCheck } from '@fortawesome/free-solid-svg-icons';
import { faRedoAlt } from '@fortawesome/fontawesome-free-solid';
import { LoaderOnConfirm } from '../../stylesheet/Loader';
import { LOADING, SUCCESS, FAILED } from '../../../constants';

const Signature = ({ investorId, onAddSignature, signCreate }) => {

  const signCanvas = useRef();

  const clear = () => signCanvas.current.clear();

  const onSave = () => {
    // onSaveSignature({sign: signCanvas.current.toDataURL('image/png'), investorId})
    onAddSignature({sign: signCanvas.current.toDataURL('image/png'), investorId})
  }

  const renderStatus = (status) => {
    if(status === SUCCESS) {
      return <FontAwesomeIcon icon={faCheck}/> //return success icon - disable clear and save
    } else if(status === FAILED) {
      return <FontAwesomeIcon icon={faRedoAlt}/> //return retry icon
    } else if(status === LOADING){
      return <LoaderOnConfirm /> //return loader icon - disable clear and save
    } else {
      return "save"
    }
  }

  const disableStatus = (status) => {
    if(status === LOADING) { //status===SUCCESS || 
      return true //return success icon - disable clear and save
    } else {
      return false
    }
  }

  return (
    <div className="signature__wrapper">
        <div className="signature__wrapper--container">
          <SignaturePad ref={signCanvas} canvasProps={{id: {investorId}}} clearOnResize={false}/>
          <div className="signature__wrapper--container--btn">
              <button id={`btn-${investorId}`} className="btn-to-link" onClick={clear} disabled={disableStatus(signCreate?.loader)}>clear</button>
              <button id={`btn2-${investorId}`} className="btn-to-link" onClick={onSave} disabled={disableStatus(signCreate?.loader)}>{renderStatus(signCreate?.loader)}</button>
          </div>
        </div>
    </div>
  )
}

export default Signature;