import React, {useState} from 'react';
import { useSelector } from 'react-redux';
import Signature from '../../Layout/Signature';
import CustomTextField from '../../Layout/CustomTextField';
import CustomRadioInput from '../../Layout/CustomRadioInput';
// import DocPlaceholder from '../../Layout/DocPlaceholder';
import { uploadSignatureApiCall } from '../../../../actions/Investor-Application-Form/uploadDocActions';
import { INIDIVIDUAL_I, JOINT_INVESTOR_I, SOLE_DIRECTOR_I, DIRECTOR_I, INDIVIDUAL_TRUSTEE_I, ATTORNEY_I, LOADING, SUCCESS, FAILED } from '../../../../constants';
import ImageUploader from '../../Layout/ImageUploader'; 
import { renderErrorTxt } from '../../../../utils/validations/Investor-Application-Form/formErrorUtils';
import moment from 'moment/moment';

const InvestorSignatory = ({ investorId, investorFormId, investorDetails, onChangeSignatory, mandatory, itemIndex }) => {
    const IS_SIGN_UPLOAD = 1;
    const IS_SIGN_CREATE = 2;
    const dateNow = moment().format();

    let base64String = "";
    const [uploadedDocs, setUploadedDocs] = useState([])
    const [signCreate, setSignCreate] = useState({file:'', loader:''})
    const [isSignCreateOrUp, setIsSignCreateOrUp] = useState('');
    const invFormStatus = useSelector((store) => store.invFormStatus);

    const errMsgs = {
        // signatory_info_id: invFormStatus.errors?.signatory_info_id? invFormStatus.errors?.signatory_info_id[itemIndex]: [''],
        full_name: invFormStatus.errors?.full_name? invFormStatus.errors?.full_name[itemIndex]: [],
        signature_url: invFormStatus.errors?.signature_url? invFormStatus.errors?.signature_url[itemIndex]: [],
        date_of_signature: invFormStatus.errors?.date_of_signature? invFormStatus.errors?.date_of_signature[itemIndex]: [],
        capacity: invFormStatus.errors?.capacity? invFormStatus.errors?.capacity[itemIndex]: []
    }

    const onChangeSignRadio = ({value}) => {
        const convrtToNum = parseInt(value);
        setIsSignCreateOrUp(convrtToNum);
    }
    
    const onInputChange = ({name, value}) => {
        onChangeSignatory({name, value, level:'individual_details', investorId});
    }

    const onChangeFn = ({name, value}) => {
        const convrtToNum = parseInt(value);
        onChangeSignatory({name:'capacity', value:convrtToNum, level:'individual_details', investorId});
    }

    const updateSignUrl = ({name, value}) => {
        onChangeSignatory({name, value, level:'individual_details', investorId});
    }

    const onSaveSignature = async({sign, investorId, fileName=""}) => {
        if(sign && investorId){
            //call api to upload files
            const formData = new FormData();
            formData.append('file', sign);
            const {success, url} = await uploadSignatureApiCall(formData);
            success && updateSignUrl({name:'signature_url', value:url});

            //below is for updating loader state for sign upload
            fileName && setUploadedDocs((prevDocs) => { //set the state of loader w.r.t file name
                return prevDocs.map((each)=>{
                    if(each.file.name === fileName){
                        return {file:each.file, loader:success? SUCCESS : FAILED}
                    }
                    return each
                })
            });

            //below is for updating loader state for sign create
            fileName || setSignCreate((prevState)=>({file:prevState?.file, loader:success? SUCCESS : FAILED}));
        }    
    }

    const onAddSignature = ({sign, investorId}) => {
        const cleanBase64 = sign.replace("data:", "").replace(/^.+,/, "");
        setSignCreate({file:cleanBase64, loader:LOADING});
        onSaveSignature({sign:cleanBase64, investorId, fileName:""})
    }

    function convertBase64Upload(file) { //to convert uploaded signature img into base64
        const fileName = file.name
        var reader = new FileReader();
        reader.onload = function () {
            base64String = reader.result.replace("data:", "").replace(/^.+,/, ""); //replace is needed - add for signature also
            onSaveSignature({sign: base64String, investorId, fileName}) //actual upload to backend
        }
        reader.readAsDataURL(file);
    }


    const onAddFiles = (files) => {
        const uploaded = [...uploadedDocs];
        files.some((file) => { 
            if(uploaded.findIndex((f)=>f.file.name === file.name) === -1){
                convertBase64Upload(file)
                uploaded.push({file, loader:LOADING});
            }
            //return a true if you want to cancel file adding and call api process -- (for example to put a limit on no.of files)
        });
        setUploadedDocs(uploaded);
    }

    const onClickRemove = (fileName) => {
        const allFiles = [...uploadedDocs];
        const newSetFiles = allFiles.filter((each)=>{
            return each.file.name !== fileName
        });
        setUploadedDocs([...newSetFiles]);
    }

    const renderSignOrUpload = () => {
        if(isSignCreateOrUp === IS_SIGN_UPLOAD) {
            // // return <DocPlaceholder onUploadFile={onUploadSignature} multiple={false} accept="image/*"/>
            // return <ImageUploader onUploadFile={onUploadSignature} multiple={false} accept="image/*"/>
            return <ImageUploader onAddFiles={onAddFiles} uploadedDocs={uploadedDocs} onClickRemove={onClickRemove} uniqId="sectionisignatureupload" multiple={false} accept="image/*"/>
        } else if(isSignCreateOrUp === IS_SIGN_CREATE) {
            return <Signature investorId={investorId} onAddSignature={onAddSignature} signCreate={signCreate}/>
        }
        return null
    }

    const renderUploadedSign = () => {
        return <img src={investorDetails?.signature_url} alt={`Investor-${investorId}`} style={{objectFit:'contain', maxWidth:'100%'}}/>
    }


  return (
    <div className="section__container--box" key={investorId}>
        <h5>{`Investor or Signatory ${itemIndex + 1}`}</h5>
        {!investorDetails?.signature_url? <>
            <div className="u-padding-10y radio-inputs-row">
                <CustomRadioInput
                    name={`signatory-${investorId}-signature`}
                    id={`signatory-${investorId}-signature-upload`}
                    content="Upload signature"
                    val={IS_SIGN_UPLOAD}
                    checked={isSignCreateOrUp === IS_SIGN_UPLOAD}
                    onChangeFn={onChangeSignRadio}
                />
                <CustomRadioInput
                    name={`signatory-${investorId}-signature`}
                    id={`signatory-${investorId}-signature-create`}
                    content="Create signature"
                    val={IS_SIGN_CREATE}
                    checked={isSignCreateOrUp === IS_SIGN_CREATE}
                    onChangeFn={onChangeSignRadio}
                />
            </div>
            {renderSignOrUpload()}
        </> : renderUploadedSign()
        }
        {errMsgs?.signature_url?.length? <div className="input__desc">
            {renderErrorTxt(errMsgs?.signature_url)}
        </div>: null}

        <div className="u-padding-10y">
            <div className="u-padding-10y">
                <CustomTextField 
                    id={`signature-${investorId}-date`}
                    label="Date"
                    placeholder="DD/MM/YYYY"
                    inputType="date"
                    type="form-input-full-width"
                    asterisk={mandatory? "*":""}
                    name='date_of_signature'
                    value={investorDetails?.date_of_signature}
                    onChangeFn={onInputChange}
                    formErrors={errMsgs}
                    maxDate={dateNow?.split("T")[0]}
                />
                <CustomTextField 
                    id={`signatory-${investorId}-fullname`}
                    label="Signatory full name"
                    placeholder="Signatory full name"
                    type="form-input-full-width"
                    asterisk={mandatory? "*":""}
                    name="full_name"
                    value={investorDetails?.full_name}
                    onChangeFn={onInputChange}
                    formErrors={errMsgs}
                />
            </div>

            <div>
                <h5>{`Signatory ${investorId} Capacity`} <span>{mandatory? "*":""}</span></h5>
                <CustomRadioInput
                    name={`signatory-${investorId}-capacity`}
                    id={`signatory-${investorId}-individual`}
                    content="Individual"
                    val={INIDIVIDUAL_I}
                    checked={investorDetails?.capacity === INIDIVIDUAL_I}
                    onChangeFn={onChangeFn}
                />
                <CustomRadioInput
                    name={`signatory-${investorId}-capacity`}
                    id={`signatory-${investorId}-joint-investor`}
                    content="Joint Investor"
                    val={JOINT_INVESTOR_I}
                    checked={investorDetails?.capacity === JOINT_INVESTOR_I}
                    onChangeFn={onChangeFn}
                />
                <CustomRadioInput
                    name={`signatory-${investorId}-capacity`}
                    id={`signatory-${investorId}-sole-director`}
                    content="Sole Director"
                    val={SOLE_DIRECTOR_I}
                    checked={investorDetails?.capacity === SOLE_DIRECTOR_I}
                    onChangeFn={onChangeFn}
                />
                <CustomRadioInput
                    name={`signatory-${investorId}-capacity`}
                    id={`signatory-${investorId}-director`}
                    content="Director"
                    val={DIRECTOR_I}
                    checked={investorDetails?.capacity === DIRECTOR_I}
                    onChangeFn={onChangeFn}
                />
                <CustomRadioInput
                    name={`signatory-${investorId}-capacity`}
                    id={`signatory-${investorId}-individual-trustee`}
                    content="Individual Trustee"
                    val={INDIVIDUAL_TRUSTEE_I}
                    checked={investorDetails?.capacity === INDIVIDUAL_TRUSTEE_I}
                    onChangeFn={onChangeFn}
                />
                <CustomRadioInput
                    name={`signatory-${investorId}-capacity`}
                    id={`signatory-${investorId}-attorney`}
                    content="Attorney"
                    val={ATTORNEY_I}
                    checked={investorDetails?.capacity === ATTORNEY_I}
                    onChangeFn={onChangeFn}
                />
            </div>
            {errMsgs?.capacity?.length? <div className="input__desc">
                {renderErrorTxt(errMsgs.capacity)}
            </div>: null}
        </div>
    </div>
  )
}

export default InvestorSignatory;