import React, { useEffect, useRef } from 'react';

const DropdownList = ({ onSelectOption, dropdownOptions=[], addClass, renderLabel, onClickVisible }) => {

  const dropdownRef = useRef(null);

  const handleClickOutside = (event) => {
    if (dropdownRef.current && !dropdownRef.current.contains(event.target)) {
      onClickVisible();
    }
  }

  useEffect(() => {

    document.addEventListener('click', handleClickOutside, true);

    return () => {
      document.removeEventListener('click', handleClickOutside, true);
    };
  }, [dropdownRef]);
 
  const renderOption = ({label, value}) => <p key={`${value}`} onClick={()=>onSelectOption({label, value})}>{renderLabel({label, value})}</p>

  const renderDropdownList = (optionsList) => optionsList.map((each)=> renderOption({label:each[1], value:each[0]}))
 
  return (
    <div className={`dropdown__main--options ${addClass}`} ref={dropdownRef}>
      {renderDropdownList(dropdownOptions)}
    </div>
  )
}

export default DropdownList;